import React, { useState, useEffect } from 'react';
import { useLocation } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVk } from "@fortawesome/free-brands-svg-icons";
import { Navbar, Nav, NavDropdown, Container } from 'react-bootstrap';
import { MainButtons } from './NavbarElement';
import { faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { Link, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

const usePathname = () => {
    let location = useLocation();
    return location.pathname;
}

const Navigationbar = () => {
    const [isDark, setIsDark] = useState('');
    useEffect(() => {
        window.addEventListener('scroll', listenScrollEvent);
        return () =>
            window.removeEventListener('scroll', listenScrollEvent);
    }, []);
    let scrollHeight = Math.max(
        document.body.scrollHeight, document.documentElement.scrollHeight,
        document.body.offsetHeight, document.documentElement.offsetHeight,
        document.body.clientHeight, document.documentElement.clientHeight
    );
    const currentPath = usePathname();
    const listenScrollEvent = (event) => {
        // console.log(scrollHeight);

        if ((window.scrollY < scrollHeight)) {
            return setIsDark("navbar-default")
        } else if (window.scrollY > scrollHeight) {
            return setIsDark("navbar-shrink")
        }

    }
    const renderMainButtons = MainButtons.map((button, i) => (
        <Nav.Link key={i}>
            <Link
                to={button.replace(/ /g, '').toLowerCase()}
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                {button.toUpperCase()}
            </Link>
        </Nav.Link>
    ))
    const scrollToTop = () => {
        scroll.scrollToTop();
    }

    return (
        <React.Fragment>
            <Navbar collapseOnSelect fixed="top" expand="sm" variant="dark" className={currentPath === '/' ? isDark : "navbar-shrink"}>
                <Container>
                    <Navbar.Brand href="/">
                        <FontAwesomeIcon icon={faVk} />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ms-auto">
                            {renderMainButtons}
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <div className="scroll-up">
                <a onClick={scrollToTop} className={`"page-scroll ${isDark === 'navbar-shrink' ? "visible" : "invisible"}`}>
                    <FontAwesomeIcon icon={faAngleUp} />
                </a>
            </div>
        </React.Fragment>
    );
};

export default Navigationbar;
