import React from 'react'
import { Container, Row, Col } from 'react-bootstrap';
import { useParams } from "react-router-dom";
import { careersList } from '../../data/careers'


const Careers = (props) => {
    const { ref } = useParams();
    const jobInfo = careersList.filter((job) => { return (Number(job.ref) === Number(ref)) })[0]

    return (
        <Container fluid className="careers-page">
            <Row className="careers-page-header text-center text-white">
                <Col className="py-5">
                    <h1>{jobInfo["title"]} | {jobInfo["Experience"]} | {jobInfo["location"]}</h1>
                </Col>
            </Row>
            <Container className="career-details">
                <Row>
                    <Col lg={8} className="job-description p-5 m-0">
                        <h3>Job Description:</h3>
                        <ul>
                            {jobInfo["Job Description"].split(". ").map((point, i) => (
                                <li>{point}</li>
                            ))}
                        </ul>
                        <h3>Primary Skills:</h3>
                        <ul>
                            <li>{jobInfo["Primary Skill"]}</li>
                        </ul>
                        <h3>Secondary Skills:</h3>
                        <ul>
                            <li>{jobInfo["Secondary Skill"]}</li>
                        </ul>

                    </Col>
                    <Col lg={4} className="job-details p-5 m-0">
                        <p><strong>Ref. </strong><br />{ref}</p>
                        <p><strong>Posted on: </strong><br />{jobInfo["Posted on"]}</p>
                        <p><strong>Experience Level: </strong><br />{jobInfo["Experience level"]}</p>
                        <p><strong>Contract type: </strong><br />{jobInfo["Contract type"]}</p>
                        <p><strong>Location </strong><br />{jobInfo["location"]}</p>
                        <p><strong>Salary </strong><br />{jobInfo["salary"]}</p>
                    </Col>
                </Row>
            </Container>
            {/* {JSON.stringify(jobInfo)} */}
        </Container>
    )
}

export default Careers
